* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input,
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button {
  width: 100%;
  background-color: #514caf;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #413c8d;
}

div {
  width: 80%;
  margin: 40px auto;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.ru {
  margin: 5px 0px 8px 0px;
}